import React,{useRef, useState, useEffect} from 'react';
import useOnScreen from '../../hooks/useOnScreen';
import {points} from '../../data';
import cn from "classnames";

import classes from './GalleryItem.module.css';

function GalleryItem({src, index, columnOffset}){
    const galleryItemRef = useRef(null);
    const [reveal, setReveal] = useState(false);
    const onScreen = useOnScreen(galleryItemRef);

    useEffect(() => {
        if (onScreen) setReveal(onScreen);
      }, [onScreen]);

    const values = points[index];
    if(!values) return null;

    const [row, column, rowSpan, colSpan] = values;

    const getSpeed = ()=>{
        if(index===1 || index===4){
            return -1;
        }
        if(index===0 || index===3){
            return 0;
        }
        return 1;
    }

    return(
        <div className={classes["gallery-item"]}
            data-scroll 
            data-scroll-speed={getSpeed()}
            style={{
                gridArea: `${row}/${column+columnOffset}/span ${rowSpan}/span ${colSpan}`
            }}
            ref={galleryItemRef}
        >

            <div className={cn("gallery-item-image", { reveal: onScreen })}>
                <div className="gallery-item-imginner" style={{backgroundImage: `url(${src})`}}></div>
            </div>
        </div>
    )
}

export default GalleryItem;