import React, {useRef, useEffect} from 'react';
import Navbar from '../layout/navbar/Navbar';
import Footer from '../layout/footer/Footer';
import {imageData} from '../data';
import GalleryItem from '../layout/galleryItem/GalleryItem';
import LocomotiveScroll from 'locomotive-scroll';
import '../../node_modules/locomotive-scroll/dist/locomotive-scroll.css'

import classes from './Home.module.css';

function Home(){

    const containerRef = useRef(null);

    useEffect(()=>{
        if(containerRef){
            new LocomotiveScroll({
                el: containerRef.current,
                smooth: true,
                direction: 'horizontal'
            })
        }
    }, [])

    const imgs = imageData.map((tupples, idx)=>
        tupples.map((url, urlIdx)=>
            <GalleryItem key={url} src={url} columnOffset={idx*14} index={urlIdx} />
        )
    )

    return(
        <>
            <Navbar />
            <div className={classes['main-container']}>
                <div ref={containerRef} className={classes["scroll-container"]} data-scroll-container>
                <div className={classes["content"]}>
                    <div className={classes["gallery"]}>
                        {imgs}
                        <div className={classes["behind-text"]+" "+classes.fill} data-scroll data-scroll-speed="1">Everybody loves good story</div>
                        <div className={classes["behind-text"]+" "+classes.outline} data-scroll data-scroll-speed='2'>Everybody loves good story</div>
                    </div>
                    <Footer />
                </div>
                </div>
            </div>
        </>
    )
}

export default Home;